import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import { Grid, InputBase, IconButton, MenuItem, FormHelperText, FormControl, Select } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';

const useStyles = makeStyles(theme => ({
    root: {
        marginBottom: 10,
        padding: 10,
        position: 'sticky',
        top: 0,
        background: theme.palette.grey[300],
        zIndex: 100,
        overflow: 'hidden',
    },
    content: { display: 'flex',
        alignItems: 'center',
    },
    search: {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
    },
    iconButton: {
        padding: 10,
    },
    button: {
        width: '100%',
    },
    formControl: {
        margin: theme.spacing(1),
        width: '100%',
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

export default function Search({ searchValue, setSearchValue, locations }) {
    const classes = useStyles();
    const [location, setLocation] = useState('');
    const [rating, setRating] = useState('');

    const handleLocation = ({ target: { value } }) => setLocation(value);
    const handleRating = ({ target: { value } }) => setRating(value);

    const handleSearchInput = ({ target: { value } }) => setSearchValue(value);

    return (
        <div className={classes.root}>
            <Grid className={classes.content} container spacing={2}>
                <Grid item xs={8}>
                    <Paper component="form" className={classes.search}>
                        <InputBase
                            className={classes.input}
                            placeholder=" ( .  )"
                            inputProps={{ 'aria-label': ' ( .  )' }}
                            value={searchValue}
                            onChange={handleSearchInput}
                        />
                        <IconButton type="submit" className={classes.iconButton} aria-label="search">
                            <SearchIcon />
                        </IconButton>
                    </Paper>
                </Grid>
                <Grid item xs={2}>
                    <FormControl className={classes.formControl}>
                        <Select value={location} onChange={handleLocation} displayEmpty className={classes.selectEmpty}>
                            <MenuItem value="" disabled>
                                
                            </MenuItem>
                            {locations.map((location, index) => (
                                <MenuItem key={index} value={index}>
                                    {location}
                                </MenuItem>
                            ))}
                        </Select>
                        <FormHelperText></FormHelperText>
                    </FormControl>
                </Grid>
                <Grid item xs={2}>
                    <FormControl className={classes.formControl}>
                        <Select value={rating} onChange={handleRating} displayEmpty className={classes.selectEmpty}>
                            <MenuItem value="" disabled>
                                
                            </MenuItem>
                            <MenuItem value={1}> </MenuItem>
                            <MenuItem value={2}> </MenuItem>
                            <MenuItem value={3}> </MenuItem>
                            <MenuItem value={4}> </MenuItem>
                            <MenuItem value={5}> </MenuItem>
                        </Select>
                        <FormHelperText></FormHelperText>
                    </FormControl>
                </Grid>
            </Grid>
        </div>
    );
}
